.nav-accent{
        color:#fff;
        background-image: -moz-linear-gradient( -67deg, rgb(109,123,244) 0%, rgb(143,112,231) 100%);
        background-image: -webkit-linear-gradient( -67deg, rgb(109,123,244) 0%, rgb(143,112,231) 100%);
        background-image: -ms-linear-gradient( -67deg, rgb(109,123,244) 0%, rgb(143,112,231) 100%);
    &__logo{
        &--text{
            color:#fff;
        }
    }
    &__hidden{
        &--svg{
            fill:#fff;
        }
    }
}