.nav{
    padding: rem(45px) 0;
    background-color: #fff;
    &__row{
        justify-content: space-between;
        align-items: center
    }
    &__logo{
        display: flex;
        align-items: center;
        font-size: rem(26px);
        color:$accent;
        font-weight: 900;
        text-transform: uppercase;
        max-height: rem(65px);
        &--text{
            margin-left: rem(8px);
        }
    }
    &__list{
        display: flex;
        align-items: center;
        @include laptop(){
            order:3;
            width: 100%;
            justify-content: center;
        }
        @include mobile(){
            display: none;
            flex-direction: column;
            align-items: center;
            padding-top: rem(40px);
        }
    }
    &__item{
        position: relative;
        padding: rem(20px);
        position: relative;
        font-size: rem(20px);
        font-weight: 700;
        z-index: 1;
        transition: all 1s ease 0s;
        &:hover:before{
            display: block;
            content:'';
            width: rem(50px);
            height: rem(50px);
            background-color: $green;
            border-radius: rem(60px);
            position: absolute;
            top:8px;
            left: 0;
            z-index: -1;
            @include mobile(){
                display: none;
            }
        }
        @include desktop(){
            font-size: rem(16px);
        }
        @include mobile(){
           text-align: center;
        }
    }
    &__btn{
        display: flex;
        align-items: center;
        height: rem(50px);
        padding: 0 rem(45px);
        border-radius: rem(50px);
        background-color: $green;
        color:#fff;
        font-weight: 500;
        &--svg{
            fill: #fff;
            width: rem(16px);
            height: rem(16px);
            margin-right: rem(8px);
        }
        @include mobile(){
            display: none;
        }
    }
    &__hidden{
        display: none;
        &--svg{
            fill:$accent;
            width: rem(40px);
            height: rem(40px);
        }
        @include mobile(){
            display: block;
        }
    }
}