.faq{
    padding: rem(50px) 0 rem(90px);
    &__title{
        font-size: rem(40px);
        font-weight: 900;
        text-align: center;
    }
    &__list{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-start;
    }
    &__hidden{
        display: none;
        padding: rem(25px) rem(30px) rem(30px);
        border-top:1px solid #d9dce9;
        font-weight: 300;
        &--active{
            display: block;
        }
    }
    &__item{
        margin: 0 auto rem(30px);
        background-color: #f6f7fb;
        border-radius: rem(10px);
        width: rem(550px);
        font-weight: 700;
        color:#686a84;
    }
    &__wrap{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: rem(10px) rem(10px) rem(10px) rem(30px);
    }
    &__btn{
        border-radius: rem(50px);
        width: rem(40px);
        height: rem(40px);
        background-image: -moz-linear-gradient( 90deg, rgb(109,123,244) 0%, rgb(143,112,231) 100%);
        background-image: -webkit-linear-gradient( 90deg, rgb(109,123,244) 0%, rgb(143,112,231) 100%);
        background-image: -ms-linear-gradient( 90deg, rgb(109,123,244) 0%, rgb(143,112,231) 100%);
        display: flex;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;
          &--svg{
              width: rem(13px);
              height: rem(13px);
              fill:#fff;
          }
    }
   
}

.faq__hidden--active .faq__hidden{
    display: block;
}

.faq__hidden--active .faq__wrap .faq__btn .faq__btn--svg{
    transform: rotate(180deg)
}