html {
  font-size: 16px;
}

html,
body {
  height: 100%;
}

body {
  font-size: 1rem;
  line-height: 1.42;
  -webkit-font-smoothing: antialiased;
  font-weight: 300;
  background: #ffffff;
  color: #5d70e3;
  font-family: 'Selene',sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: normal;
  font-weight: 900;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

p {
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

* {
  box-sizing: border-box;
  outline: none;
}

.container {
  max-width: 1170px;
  width: 100%;
  padding: 0 15px;
  margin: auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.col {
  width: 100%;
}

.nav {
  padding: 2.8125rem 0;
  background-color: #fff;
}

.nav__row {
  justify-content: space-between;
  align-items: center;
}

.nav__logo {
  display: flex;
  align-items: center;
  font-size: 1.625rem;
  color: #5d70e3;
  font-weight: 900;
  text-transform: uppercase;
  max-height: 4.0625rem;
}

.nav__logo--text {
  margin-left: 0.5rem;
}

.nav__list {
  display: flex;
  align-items: center;
}

.nav__item {
  position: relative;
  padding: 1.25rem;
  position: relative;
  font-size: 1.25rem;
  font-weight: 700;
  z-index: 1;
  transition: all 1s ease 0s;
}

.nav__item:hover:before {
  display: block;
  content: '';
  width: 3.125rem;
  height: 3.125rem;
  background-color: #3ad29f;
  border-radius: 3.75rem;
  position: absolute;
  top: 8px;
  left: 0;
  z-index: -1;
}

.nav__btn {
  display: flex;
  align-items: center;
  height: 3.125rem;
  padding: 0 2.8125rem;
  border-radius: 3.125rem;
  background-color: #3ad29f;
  color: #fff;
  font-weight: 500;
}

.nav__btn--svg {
  fill: #fff;
  width: 1rem;
  height: 1rem;
  margin-right: 0.5rem;
}

.nav__hidden {
  display: none;
}

.nav__hidden--svg {
  fill: #5d70e3;
  width: 2.5rem;
  height: 2.5rem;
}

.nav-accent {
  color: #fff;
  background-image: -moz-linear-gradient(-67deg, #6d7bf4 0%, #8f70e7 100%);
  background-image: -webkit-linear-gradient(-67deg, #6d7bf4 0%, #8f70e7 100%);
  background-image: -ms-linear-gradient(-67deg, #6d7bf4 0%, #8f70e7 100%);
}

.nav-accent__logo--text {
  color: #fff;
}

.nav-accent__hidden--svg {
  fill: #fff;
}

.header {
  overflow: hidden;
  color: #fff;
  position: relative;
  padding-top: 5.3125rem;
  padding-bottom: 11.25rem;
  background-image: url(../img/bg.png);
  background-position: center bottom;
  background-size: cover;
  min-height: 600px;
}

.header__title {
  font-size: 3rem;
}

.header__title--span {
  display: block;
}

.header__title--second {
  margin-left: 6.5625rem;
}

.header__title--third {
  margin-left: 12.8125rem;
}

.header__desc {
  font-size: 1.625rem;
  font-weight: 300;
  line-height: 2.625rem;
  max-width: 48.125rem;
}

.header__deco {
  font-size: 2.25rem;
  line-height: 2.625rem;
  margin-top: 2.25rem;
}

.header__img {
  position: absolute;
  max-height: 35.625rem;
}

.header__apps {
  display: flex;
  margin-top: 2.5rem;
  flex-wrap: wrap;
}

.header__apps--a-store {
  margin-right: 1.25rem;
}

.prices {
  color: #fff;
  padding: 7.5rem 0 10.9375rem;
  background-image: url(../img/bg-2.png);
  background-position: center;
  background-size: cover;
}

.prices__title {
  padding-top: 7.5rem;
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 1.875rem;
}

.prices__desc {
  text-align: center;
  font-size: 1.25rem;
  max-width: 43.125rem;
  margin: auto;
}

.prices__pack {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.prices__pack--item {
  position: relative;
  padding: 3.125rem 2.5rem;
  max-width: 22.5rem;
  height: 25.3125rem;
  text-align: center;
  border-radius: 0.625rem;
  background-color: #fff;
  color: #707070;
  font-size: 1.125rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto 1.875rem;
}

.prices__pack--title {
  color: #5d70e3;
  font-size: 2.25rem;
  font-weight: 900;
  margin-bottom: 1.25rem;
}

.prices__pack--btn {
  background-color: #5d70e3;
  border-radius: 3.125rem;
  height: 3.125rem;
  display: flex;
  align-items: center;
  max-width: 11.875rem;
  color: #fff;
  text-transform: uppercase;
  font-weight: 300;
  justify-content: center;
  margin-bottom: 0;
  margin-top: auto;
  align-content: flex-end;
  flex-shrink: 0;
  padding: 0 45px;
}

.prices__pack--desc {
  max-width: 15rem;
  margin-bottom: 2.1875rem;
}

.prices__pack--price {
  margin-top: 2.5rem;
  font-size: 1.5rem;
}

.prices__pack--bold {
  font-weight: 900;
  font-size: 2.25rem;
}

.prices__advantages {
  justify-content: space-around;
  font-size: 1.125rem;
  max-width: 52.1875rem;
  margin: 2.8125rem auto 3.125rem;
  display: flex;
  flex-wrap: wrap;
  padding: 1.875rem;
  border-radius: 10px;
  background-image: -moz-linear-gradient(90deg, #ffa658 0%, #fec96b 100%);
  background-image: -webkit-linear-gradient(90deg, #ffa658 0%, #fec96b 100%);
  background-image: -ms-linear-gradient(90deg, #ffa658 0%, #fec96b 100%);
}

.prices__advantages--item {
  display: flex;
  align-items: center;
  margin-bottom: 0.3125rem;
}

.prices__advantages--svg {
  margin-right: 0.75rem;
  height: 1.125rem;
  width: 1.125rem;
  fill: #fff;
}

.prices__advantages--col {
  margin-right: 1.875rem;
}

.prices__advantages--col:last-child {
  margin-right: 0;
}

.prices__info {
  margin-top: 4.375rem;
  font-size: 1.25rem;
  text-align: center;
  font-weight: 300;
  padding-bottom: 10.9375rem;
}

.advantages__title {
  font-size: 2.5rem;
  font-weight: 900;
  text-align: center;
  margin-bottom: 5rem;
}

.advantages__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.advantages__item {
  position: relative;
  margin-left: auto;
  margin-bottom: 3.75rem;
  font-weight: 700;
  text-align: center;
  max-width: 18.75rem;
  font-size: 1.5rem;
}

.advantages__item:first-child {
  margin-left: 0;
}

.advantages__item:last-child {
  margin: 0 auto;
}

.advantages__item--svg {
  height: 19.0625rem;
  margin-bottom: 1.875rem;
}

.advantages__item--circles {
  position: absolute;
  right: -80px;
  top: 50%;
}

.advantages__item--circles-last {
  right: -130px;
}

.onboard__wrap {
  position: relative;
  overflow: hidden;
  width: 100%;
  color: #fff;
  background-image: -moz-linear-gradient(90deg, #8f70e7 0%, #617ff9 100%);
  background-image: -webkit-linear-gradient(90deg, #8f70e7 0%, #617ff9 100%);
  background-image: -ms-linear-gradient(90deg, #8f70e7 0%, #617ff9 100%);
  border-radius: 0.625rem;
  padding: 5rem 4.375rem;
}

.onboard__title {
  font-weight: 900;
  font-size: 2.5rem;
  margin-bottom: 1.875rem;
}

.onboard__text {
  max-width: 35.625rem;
  font-size: 1.25rem;
  font-weight: 500;
}

.onboard__img {
  position: absolute;
  top: 2.5rem;
  right: -6.875rem;
  max-height: 21.875rem;
}

.info {
  padding: 5.625rem 0;
}

.info__title {
  text-align: center;
  font-size: 2.5rem;
  font-weight: 900;
}

.info__text {
  font-size: 1.125rem;
  font-weight: 700;
  color: #686a84;
  max-width: 62.5rem;
  margin: auto;
  text-align: center;
}

.faq {
  padding: 3.125rem 0 5.625rem;
}

.faq__title {
  font-size: 2.5rem;
  font-weight: 900;
  text-align: center;
}

.faq__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}

.faq__hidden {
  display: none;
  padding: 1.5625rem 1.875rem 1.875rem;
  border-top: 1px solid #d9dce9;
  font-weight: 300;
}

.faq__hidden--active {
  display: block;
}

.faq__item {
  margin: 0 auto 1.875rem;
  background-color: #f6f7fb;
  border-radius: 0.625rem;
  width: 34.375rem;
  font-weight: 700;
  color: #686a84;
}

.faq__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.625rem 0.625rem 0.625rem 1.875rem;
}

.faq__btn {
  border-radius: 3.125rem;
  width: 2.5rem;
  height: 2.5rem;
  background-image: -moz-linear-gradient(90deg, #6d7bf4 0%, #8f70e7 100%);
  background-image: -webkit-linear-gradient(90deg, #6d7bf4 0%, #8f70e7 100%);
  background-image: -ms-linear-gradient(90deg, #6d7bf4 0%, #8f70e7 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.faq__btn--svg {
  width: 0.8125rem;
  height: 0.8125rem;
  fill: #fff;
}

.faq__hidden--active .faq__hidden {
  display: block;
}

.faq__hidden--active .faq__wrap .faq__btn .faq__btn--svg {
  transform: rotate(180deg);
}

.breadcrumbs__link {
  text-decoration: underline;
}

.breadcrumbs__link--unactive {
  color: #9a9eb2;
  text-decoration: none;
  cursor: default;
}

.footer {
  color: #9b9db3;
  background-color: #191a1f;
  padding: 4.0625rem 0;
}

.footer__row {
  justify-content: space-between;
}

.footer__logo {
  color: #fff;
  display: flex;
  align-items: center;
  font-size: 1.625rem;
  font-weight: 900;
  text-transform: uppercase;
  max-height: 4.0625rem;
  margin-bottom: 2.8125rem;
}

.footer__logo--text {
  margin-left: 0.5rem;
}

.footer__info {
  display: flex;
  width: 100%;
  margin-bottom: 3.125rem;
  flex-wrap: wrap;
}

.footer__adress {
  max-width: 11.875rem;
  line-height: 1.625rem;
}

.footer__nav {
  margin-right: 4.0625rem;
  line-height: 1.625rem;
}

.footer__emails {
  margin-right: 4.0625rem;
  line-height: 1.625rem;
}

.footer__map {
  width: 450px;
  height: 100%;
  border-radius: 10px;
  overflow: hidden;
}

.footer__bottom {
  display: flex;
}

.footer__social {
  display: flex;
}

.footer__icon {
  padding: 0.3125rem;
}

.footer__icon--svg {
  max-height: 1rem;
  max-width: 1rem;
  fill: #9b9db3;
}

.footer__icon--svg:hover {
  fill: #5d70e3;
}

.footer__copyright {
  margin-right: 3.75rem;
}

.footer__link:hover {
  text-decoration: underline;
}

@media screen and (max-width: 1200px) {
  .nav__item {
    font-size: 1rem;
  }

  .header__title--second {
    margin-left: 0;
  }

  .header__title--third {
    margin-left: 0;
  }

  .header__img {
    display: none;
  }

  .advantages__item:first-child {
    margin: auto;
  }

  .advantages__item--circles {
    display: none;
  }

  .advantages__item {
    margin: 0 auto 3.75rem;
  }

  .onboard__img {
    right: -30%;
  }

  .footer__logo {
    justify-content: center;
  }

  .footer__info {
    justify-content: center;
  }

  .footer__right {
    width: 100%;
  }

  .footer__left {
    width: 100%;
  }

  .footer__map {
    width: 100%;
    height: 31.25rem;
    margin-top: 3.75rem;
  }

  .footer__bottom {
    justify-content: center;
  }
}

@media screen and (max-width: 992px) {
  .nav__list {
    order: 3;
    width: 100%;
    justify-content: center;
  }

  .onboard__title {
    text-align: center;
  }

  .onboard__text {
    text-align: center;
    margin: 0 auto;
  }

  .onboard__img {
    display: none;
  }
}

@media screen and (max-width: 768px) {
  html {
    font-size: 15px;
  }

  .prices {
    padding-top: 18.75rem;
  }

  .prices__pack--btn {
    max-width: none;
  }
}

@media screen and (max-width: 578px) {
  .nav__list {
    display: none;
    flex-direction: column;
    align-items: center;
    padding-top: 2.5rem;
  }

  .nav__item:hover:before {
    display: none;
  }

  .nav__item {
    text-align: center;
  }

  .nav__btn {
    display: none;
  }

  .nav__hidden {
    display: block;
  }

  .prices__advantages {
    max-width: 20rem;
    justify-content: flex-start;
  }

  .footer__info {
    text-align: center;
    flex-direction: column;
    align-items: center;
  }

  .footer__nav {
    margin-right: 0;
    margin-bottom: 1.875rem;
  }

