.prices{
    color:#fff;
    padding: rem(120px) 0 rem(175px);
    background-image: url(../img/bg-2.png);
    background-position: center;
    background-size: cover;
    @include tablets(){
       padding-top: rem(300px);
    }  
    &__title{
        padding-top: rem(120px);
        text-align: center;
        font-size: rem(40px);
        margin-bottom: rem(30px);
    }
    &__desc{
        text-align: center;
        font-size: rem(20px);
        max-width: rem(690px);
        margin: auto
    }
    &__pack{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        &--item{
            position: relative;
            padding: rem(50px) rem(40px);
            max-width: rem(360px);
            height: rem(405px);
            text-align: center;
            border-radius: rem(10px);
            background-color: #fff;
            color:#707070;
            font-size: rem(18px);
            display: flex;
            flex-direction: column;
            align-items: center;
            margin: 0 auto rem(30px);
        }
        &--title{
            color:$accent;
            font-size: rem(36px);
            font-weight: 900;
            margin-bottom: rem(20px);
        }
        &--btn{
            background-color: $accent;
            border-radius: rem(50px);
            height: rem(50px);
            display: flex;
            align-items: center;
            max-width: rem(190px);
            color:#fff;
            text-transform: uppercase;
            font-weight: 300;
            justify-content: center;
            margin-bottom: 0;
            margin-top: auto;
            align-content: flex-end;
            flex-shrink: 0;
            padding: 0 45px;
            @include tablets(){
                max-width: none;
            }   
        }
        &--desc{
            max-width: rem(240px);
            margin-bottom: rem(35px);
        }
        &--price{
            margin-top: rem(40px);
            font-size: rem(24px);
        }
        &--bold{
            font-weight: 900;
            font-size: rem(36px);
        }
    }
    &__advantages{
        justify-content: space-around;
        font-size: rem(18px);
        max-width: rem(835px);
        margin: rem(45px) auto rem(50px);
        display: flex;
        flex-wrap: wrap;
        padding: rem(30px);
        border-radius: 10px;
        background-image: -moz-linear-gradient( 90deg, rgb(255,166,88) 0%, rgb(254,201,107) 100%);
        background-image: -webkit-linear-gradient( 90deg, rgb(255,166,88) 0%, rgb(254,201,107) 100%);
        background-image: -ms-linear-gradient( 90deg, rgb(255,166,88) 0%, rgb(254,201,107) 100%);      
        @include mobile(){
            max-width: rem(320px);
            justify-content: flex-start;
        }   
        &--item{
            display: flex;
            align-items: center;
            margin-bottom: rem(5px);

        } 
        &--svg{
            margin-right: rem(12px);
            height: rem(18px);
            width: rem(18px);
            fill:#fff
        }
        &--col{
            margin-right: rem(30px);
            &:last-child{
                margin-right: 0;
            }
        }
    }
    &__info{
        margin-top: rem(70px);
        font-size: rem(20px);
        text-align: center;
        font-weight: 300;
        padding-bottom: rem(175px);
    }
   
}