.onboard{
    &__wrap{
        position: relative;
        overflow: hidden;
        width: 100%;
        color:#fff;
        background-image: -moz-linear-gradient( 90deg, rgb(143,112,231) 0%, rgb(97,127,249) 100%);
        background-image: -webkit-linear-gradient( 90deg, rgb(143,112,231) 0%, rgb(97,127,249) 100%);
        background-image: -ms-linear-gradient( 90deg, rgb(143,112,231) 0%, rgb(97,127,249) 100%);     
        border-radius: rem(10px);
        padding: rem(80px) rem(70px);
    }    
    &__title{
        font-weight: 900;
        font-size: rem(40px);
        margin-bottom: rem(30px);
        @include laptop(){
            text-align: center;
        }  
    } 
    &__text{
        max-width: rem(570px);
        font-size: rem(20px);
        font-weight: 500;
        @include laptop(){
            text-align: center;
            margin: 0 auto;
        }  
    } 
    &__img{
        position: absolute;
        top: rem(40px);
        right: rem(-110px);
        max-height: rem(350px);
        @include desktop(){
            right:-30%
        }  
        @include laptop(){
            display: none;
        }  
    }
}