.header{
    overflow: hidden;
    color:#fff;
    position: relative;
    padding-top: rem(85px);
    padding-bottom: rem(180px);
    background-image: url(../img/bg.png);
    background-position: center bottom;
    background-size: cover;
    min-height: 600px;
    &__title{
        font-size: rem(48px);
        &--span{
            display: block;
        }
        &--second{
            margin-left: rem(105px);
            @include desktop(){
            margin-left: 0;
        }
        }
        &--third{
            margin-left: rem(205px);
            @include desktop(){
            margin-left: 0;
        }
        }
    }
    &__desc{
        font-size: rem(26px);
        font-weight: 300;
        line-height: rem(42px);
        max-width: rem(770px);
    }
    &__deco{
        font-size: rem(36px);
        line-height: rem(42px);
        margin-top: rem(36px);
    }
    &__img{
        position: absolute;
        max-height: rem(570px);
        @include desktop(){
            display: none;
        }
    }
    &__apps{
        display: flex;
        margin-top: rem(40px);
        flex-wrap: wrap;
        &--a-store{
            margin-right: rem(20px);
        }
    }
}