.advantages{
    &__title{
        font-size: rem(40px);
        font-weight: 900;
        text-align: center;
        margin-bottom: rem(80px);
    }
    &__list{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    &__item{
        position: relative;
        margin-left: auto;
        margin-bottom: rem(60px);
        font-weight: 700;
        text-align: center;
        max-width: rem(300px);
        font-size: rem(24px);
        &:first-child{
            margin-left: 0;
            @include desktop(){
                margin:  auto
            }  
        }
        &:last-child{
            margin: 0 auto;
            
        }
        &--svg{
            height: rem(305px);
            margin-bottom: rem(30px);
        }
        &--circles{
            position: absolute;
            right: -80px;
            top: 50%;
            &-last{
                right: -130px
            }
            @include desktop(){
                display: none;
            }   
        }
        @include desktop(){
            margin: 0 auto rem(60px)
        }   
    }

}