.footer{
    color:#9b9db3;
    background-color: #191a1f;
    padding: rem(65px) 0;
    &__row{
        justify-content: space-between
    }
    &__logo{
        color:#fff;
        display: flex;
        align-items: center;
        font-size: rem(26px);
        font-weight: 900;
        text-transform: uppercase;
        max-height: rem(65px);
        margin-bottom: rem(45px);
        &--text{
            margin-left: rem(8px);
        }
        @include desktop(){
            justify-content: center
        }  
    }
    &__info{
        display: flex;
        width: 100%;
        margin-bottom: rem(50px);
        flex-wrap: wrap;
        @include desktop(){
            justify-content: center;
        }  
        @include mobile(){
            text-align: center;
            flex-direction: column;
            align-items: center;
        }
    }
    &__adress{
        max-width: rem(190px);
        line-height: rem(26px);
    }
    &__nav{
        margin-right: rem(65px);
        line-height: rem(26px);
        @include mobile(){
            margin-right: 0;
            margin-bottom: rem(30px);
        }
    }
    &__emails{
        margin-right: rem(65px);
        line-height: rem(26px);
        @include mobile(){
            margin-right: 0;
            margin-bottom: rem(30px);
        }
    }
    &__right{
        @include desktop(){
            width: 100%;
        }  
    }
    &__left{
        @include desktop(){
            width: 100%;
        }  
    }
    &__map{
        width: 450px;
        height: 100%;
        border-radius: 10px;
        overflow: hidden;
        @include desktop(){
            width: 100%;
            height: rem(500px);
            margin-top: rem(60px);
        }  
    }
    &__bottom{
        display: flex;
        @include desktop(){
            justify-content: center
        }  
    }
    &__social{
        display: flex;
    }
    &__icon{
        padding: rem(5px);
        &--svg{
            max-height: rem(16px);
            max-width: rem(16px);
            fill:#9b9db3;
            &:hover{
                fill:$accent
            }
        }
    }
    &__copyright{
        margin-right: rem(60px);
    }
    &__link{
        &:hover{
            text-decoration: underline;
        }
    }
}